<template lang="en">
<div class="my-popups ">
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">
                        {{$t('Privilege')}}
                    </h5>
                    <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                </div>
                <div class="modal-body">
                    <div class="listing">
                        <div class="table-responsive table-fix-header">
                            <table id="tblReturnRequestBulk" class="table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">
                                    <tr>
                                        <th>{{$t('Privilege')}}</th>
                                        <th class="text-center">{{$t('Action')}}</th>                                       
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(item,index) in ModuleName" :key="index">
                                        <td> {{item.privilege_desc}}</td>
                                        <td class="text-center">
                                            <div class="custom-control custom-checkbox">
                                                <input type="checkbox" v-on:click="checkedValue(item.privilege_id)" class="custom-control-input chkItems" :value="item.privilege_id" id="chk_2" style="display: none;" :class="{'chkItems':item.sorting_order==1}">
                                                <label class="custom-control-label universal-custom-control-label pt-1" for="chk_2"></label>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                        <div class="form-group">
                            <button type="submit" v-on:click="save" class="btn btn-info float-right"><i class='fa fa-save pr-2'></i>Save</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from "../../service/DataService";

export default {
    props: {
        moduelId: {},
        subModuleId: {},
        comment:[],
       
    },
    data() {
        return {
            ModuleName: [],
        }
    },
    created: function () {
        this.GetPrivilegeListFeature();
    },
    methods: {
        ClosePopup: function () {
            this.$parent.IsModule = false;
        },
        async GetPrivilegeListFeature() {
            var vm = this;
            vm.isLoading = true;
            var url = `userTypeId=1&moduleId=${vm.moduelId}&subModuleId=${vm.subModuleId}&privilegeGroupCode=&roleId=`;
            await DataService.GetPrivilegeListForFeature(url).then(response => {
                vm.ModuleName = response.data.PrivilegeVertical;
                console.log(vm.ModuleName);
                vm.isLoading = false;
            })
            setTimeout(function () {
                vm.CheckBoxBootstrap();
            }, 100);
        },
     save(){             
            var vm = this;
            var PrivilageAarryval = [];           
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {               
                PrivilageAarryval.push({
                    privilege_id:$(this).val(),
                    is_enable:1
                })
            });
            vm.$emit('Check-Privilege',PrivilageAarryval);
            vm.ClosePopup();
     }


    }
}
</script>
