<template lang="en">
<div class="my-popups">
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">
                        {{$t('AddFeature')}}
                    </h5>
                    <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                </div>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-2">
                    <div class="p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                        <span class="mandatory">{{$t('MandatoryString')}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <PrivilegePopUp :moduelId="moduelId" :subModuleId="subModuleId" v-if="IsModule" @Check-Privilege="CheckPrivilege"></PrivilegePopUp>
</div>
</template>

<script>
import DataService from "../../service/DataService";
import PrivilegePopUp from '../../components/Modal/PrivilegePopUp.vue'
export default {
    components: {
        PrivilegePopUp
    },
    data() {
        return {
            ModuleName: [],
            IsModule: false,
            CheckPrivilegeValue: '',
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: 'Module Name',
                            name: "ModuleName",
                            value: "",
                            validationRules: {
                                "required": true
                            },
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.GetSubModule
                            },
                        },
                        {
                            astype: "SelectField",
                            label: 'Sub Module',
                            name: "SubModule",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            config: {
                                options: [],

                            },
                        }
                    ]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('Based On'),
                            name: "BasedOn",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            config: {
                                options: [{
                                    value: "Module",
                                    name: "Module"
                                }, {
                                    value: "Privilege",
                                    name: "Privilege"
                                }],
                                onChange: this.GetPrivilege
                            },
                        },
                        {
                            astype: 'TextField',
                            label: this.$t('Feature Name'),
                            name: "FeatureName",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    //  onClick:this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.ClosePopup
                }
            ],
        }
    },
    created() {
        this.GetModuleName();
    },
    methods: {
        async CheckPrivilege(value) {
            this.CheckPrivilegeValue = value;
        },
        async GetModuleName() {
            var vm = this;
            vm.isLoading = true;
            var url = `moduleId=&userTypeId=&requestFrom=&isCustomFieldEnable`;
            await DataService.GetModuleList(url).then(response => {
                    vm.AssetType = response.data.Data;
                    console.log(vm.AssetType);
                    vm.AssetType.forEach((itm) => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: itm.module_name,
                            value: itm.module_id
                        })
                    })
                    vm.isLoading = false;
                })
                .catch((err) => {
                    console.error(err);
                });
        },

        async GetSubModule(evt) {
            var vm = this;
            vm.isLoading = true;
            var url = `moduleId=${evt.target.value}&userTypeId=&requestFrom=&isCustomFieldEnable`;
            await DataService.GetModuleList(url).then(response => {
                    vm.ModuleName = response.data.Data;
                    console.log(vm.ModuleName);
                    vm.ModuleName.forEach((item) => {
                        vm.FormSchema[0].Data[1].config.options.push({
                            name: item.sub_module_name,
                            value: item.sub_module_id
                        })
                    })
                    vm.isLoading = false;
                })
                .catch((err) => {
                    console.error(err);
                });
        },
        ClosePopup: function () {
            this.$parent.IsAddFeature = false;
        },
        async GetPrivilege(evt) {
            if (evt.target.value == 'Privilege') {
                this.moduelId = this.FormSchema[0].Data[0].value;
                this.subModuleId = this.FormSchema[0].Data[1].value;
                this.IsModule = true;
            } else {
                this.IsModule = false;
            }
        },
        async onSubmit(formfields, e) {
            
            var vm = this;
            vm.isLoading = true;
            var obj = {
                feature_id: 0,
                module_id: formfields.ModuleName,
                sub_module_id: formfields.SubModule,
                feature_based_on: formfields.BasedOn,
                feature_name: formfields.FeatureName,
                status_id: 1001,
                comment: formfields.Description,
                postString: JSON.stringify(vm.CheckPrivilegeValue),
            }
            var url = `feature_id=${obj.feature_id}&module_id=${obj.module_id}&sub_module_id=${obj.sub_module_id}&feature_based_on=${obj.feature_based_on}&feature_name=${obj.feature_name}&status_id=${obj.status_id}&comment=${obj.comment}&postString=${obj.postString}`;
            await DataService.SaveAdvancedFeature(url).then(response => {
                vm.isLoading = false;
                if (response.data != null) {
                    vm.ClosePopup();
                    vm.ShowAlert(vm.$t('PrivilegeSave'), "success", true, vm.$t('Alert'));
                    vm.$router.push({name: "ModuleFeature"});
                } else {
                    vm.ShowAlert(vm.$t('AddedError'), "failure",false, vm.$t("Alert"));
                }
            })
        },
    }
}
</script>
